    height: 100%;
} */

.feedbackForm__close--out {
    display: none;
}

.form {
    position: fixed;
    /* overflow-y: scroll; */
    overflow-x: hidden;
    right: -100%;
    opacity: 0;
    transition: all 0.5s ease-out;
    top: 0;
    bottom: 0;
    width: 320px;
    height: 100%;
    /* background-color: #fff; */
    box-shadow: -16px 0 52px 0 rgba(14, 24, 80, 0.2);
    z-index: 2;
    display: flex;
}

.form__general {
    background-color: #fff;
    padding-top: 24px;
    box-shadow: -16px 0 52px 0 rgba(14, 24, 80, 0.2);
    width: 320px;
    height: 100%;
}

.rightForm--open {
    right: 0;
    opacity: 1;
    transition: all 0.5s ease-out;
}

.top {
    margin-left: 16px;
    margin-right: 24px;
    display: flex;
}

.feedbackForm__close {
    cursor: pointer;
}


.img--middle {
    vertical-align: middle;
}

.form__title {
    color: #1B1C21;
    font-family: TT Lakes;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: -0.6px;
}

.form__title::before {
    content: url(./static/highlight.svg);
    vertical-align: middle;
    margin-right: 12px;
}

.form__main {
    margin-left: 16px;
    width: 288px;
}

input {
    width: 288px;
    height: 48px;
    border-radius: 8px;
    border: 1px solid #EAEAEA;
    padding-left: 12px;
    margin-bottom: 16px;
}

textarea {
    width: 288px;
    height: 118px;
    border-radius: 8px;
    border: 1px solid #EAEAEA;
    padding-left: 12px;
    padding-top: 12px;
}

input::placeholder {
    color: #B5B6BC;
    font-family: TT Lakes, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.2px;
}

textarea::placeholder {
    color: #B5B6BC;
    font-family: TT Lakes, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.2px;
}

.conf {
    color: #7E7E82;
    font-family: TT Lakes, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
    margin-top: 16px;
    margin-bottom: 23px;
}

.conf a {
    color: #FF3E79;
    font-family: TT Lakes;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
}

.form__button {
    display: flex;
    align-items: center;
    padding-left: 16px;
    margin-left: auto;
    width: 136px;
    height: 32px;
    border-radius: 16px;
    background: #FF3E79;
    border: none;

    color: #FFF;
    font-family: TT Lakes;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px; /* 200% */
    letter-spacing: -0.15px;
    text-transform: uppercase;

}

@media screen and (1120px<=width) {
    .form, .form__general {
        width: 440px;
    }

    .form__main {
        width: 376px;
    }
    
    input, textarea {
        width: 376px;
    }
}

@media screen and (1440px<=width) {

    .form {
        box-shadow: none;
        width: auto;
    }

    .feedbackForm__close {
        display: none;
    }
    
    .feedbackForm__close--out {
        display: flex;
        width: 40px;
        height: 40px;
        margin-right: 30px;
        margin-top: 32px;
        /* position: absolute;
        top: 32px;
        right: 473px; */
    }
}