.main__box {
    width: 100%;
    height: 80px;
    background: linear-gradient(to right, rgba(248,248,248,0), white);
    padding-top: 16px;
    margin-bottom: 16px;
}

.main__title {
    margin: 0;
    /* padding-top: 0; */
    margin-left: 16px;
    color: #7e7e82;
    font-size: 16px;
    font-family: TT Lakes, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.2px;
    text-transform: uppercase;
    /* margin-top: auto;
    margin-bottom: auto; */
}

.brands__fade {
    position: absolute;
    right: 0;
    top: 700px;
    z-index: 1;
}


.swiper-wrapper {
    margin-left: 16px;
    padding-bottom: 40px;
}

.swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    line-height: 12px;
    background-color: #DDDDDD;
}

.swiper-pagination-bullet {
    background-color: #B5B6BC;
}

.swiper-slide {
    padding: 0;
    width: auto;
}

.swiper-slide {
    cursor: pointer;
}

.swiper-slide:hover {
    border-radius: 6px;
    box-shadow: 0 7px 20px -15px #000;
}

.brands__img {
    vertical-align: middle;
}

.main__button--change {
    margin: 0;
    padding: 0;
    color: #1B1C21;
    font-size: 16px;
    font-family: TT Lakes, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.2px;
    vertical-align: middle;
    text-align: center;
    display: none;
    cursor: pointer;
}

.active {
    margin: 0;
    padding: 0;
    color: #1B1C21;
    font-size: 16px;
    font-family: TT Lakes, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 500;
    line-height: 24px;
    letter-spacing: -0.2px;
    vertical-align: middle;
    text-align: center;
    cursor: pointer;
}

@media screen and (768px<=width<1120px) {
    .main__title {
        margin-left: 24px;
    }

    .main__brands {
        margin-top: 32px;
    }

    .main__box {
        height: 56px;
    }
    
    .swiper--hidden {
        width: auto;
        height: 170px;
        overflow: hidden;
        transition: all 1s ease-in;
    }

    .swiper--open {
        width: auto;
        height: fit-content;
        transition: all 1s ease-in;
    }

    .swiper-wrapper {
        display: flex;
        flex-wrap: wrap;
        row-gap: 16px;
        column-gap: 10px;
        margin-left: 24px;
    }

    .brands__fade {
        display: none;
    }

    .main__button--change {
        display: inline-block;
        margin-left: 24px;
    }

    .active {
        display: inline-block;
        margin-left: 24px;
    }

    .main__button--change::before {
        content: url(./static/expand.svg);
        width: 24px;
        height: 24px;
        vertical-align: middle;
        margin-right: 8px;
    }

    .active::before {
        content: url(./static/expand180.svg);
        width: 24px;
        height: 24px;
        vertical-align: middle;
        margin-right: 8px;
    }

}

@media screen and (1120px<=width) {
    .main__brands {
        margin-top: 51px;
    }

    .main__box {
        height: 56px;
    }
    
    .main__title {
        margin-left: 32px;
    }

    .swiper--hidden {
        width: auto;
        height: 170px;
        overflow: hidden;
    }

    .swiper--open {
        width: auto;
        height: fit-content;
        overflow: visible;
    }

    .swiper-wrapper {
        display: flex;
        flex-wrap: wrap;
        row-gap: 16px;
        column-gap: 32px;
        margin-left: 32px;
    }

    .brands__fade {
        display: none;
    }
    
    .main__button--change {
        display: inline-block;
        margin-left: 32px;
    }

    .active {
        display: inline-block;
        margin-left: 32px;
    }

    .main__button--change::before {
        content: url(./static/expand.svg);
        width: 24px;
        height: 24px;
        vertical-align: middle;
        margin-right: 8px;
        transform: rotate(180deg);
    }

    .active::before {
        content: url(./static/expand180.svg);
        width: 24px;
        height: 24px;
        vertical-align: middle;
        margin-right: 8px;
    }
}

