.callForm__close--out {
    display: none;
}


.rightForm--open {
    right: 0;
    opacity: 1;
    transition: all 0.5s ease-out;
}



.callForm__close {
    cursor: pointer;
}


@media screen and (1440px<=width) {

    .form {
        box-shadow: none;
    }

    .callForm__close {
        display: none;
    }
    
    .callForm__close--out {
        display: flex;
        width: 40px;
        height: 40px;
        margin-right: 30px;
        margin-top: 32px;
    }
}