/* html, body {
    height: 100%;
} */

.leftMenu {
    position: fixed;
    overflow-y: scroll;
    left: -100%;
    opacity: 0;
    transition: all 0.5s ease-out;
    top: 0;
    bottom: 0;
    width: 320px;
    height: 100%;
    background-color: #fff;
    padding-top: 24px;
    box-shadow: 16px 0 52px 0 rgba(14, 24, 80, 0.2);
    z-index: 2;
}

.leftMenu--open {
    left: 0;
    opacity: 1;
    transition: all 0.5s ease-out;
}

.leftMenu__top {
    margin-left: 24px;
    margin-right: 24px;
    display: flex;
}

.leftMenu__close {
    cursor: pointer;
}

.leftMenu__logo {
    margin-left: 16px;
}

.leftMenu__search {
    margin-left: auto;
}

.img--middle {
    vertical-align: middle;
}

.leftMenu__links {
    margin: 0;
    padding: 0;
    padding-top: 50px;
}

.leftMenu__item {
    list-style: none;
    margin-top: 24px;
}

.leftMenu__item:first-child {
    margin-top: 0;
}


.leftMenu__link {
    text-decoration: none;
    color: #1b1c21;
    font-family: TTLakes, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    margin-left: 24px;
}

.leftMenu__link--active {
    color: #7e7e82;
}

.leftMenu__item--active::before {
    content: url(./static/highlight32.svg); 
    vertical-align: middle;
}

.menu__buttons {
    display: flex;
    margin-top: 268px;
}

.menu__buttons * {
    cursor: pointer;
}

.leftMenu__search {
    cursor: pointer;
}

.menu__item {
    margin-left: 16px;
}

.menu__item:first-child {
    margin-left: 24px;
}

.leftMenu__mail {
    font-family: TTLakes, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    margin-left: 24px;
    margin-top: 16px;
}

.leftMenu__phone {
    font-family: TTLakes, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    margin-left: 24px;
    margin-top: 12px;
}

.leftMenu__languages {
    display: flex;
    margin: 0;
    margin-left: 24px;
    padding: 0;
    padding-bottom: 32px;
}

.languages__item {
    font-family: TTLakes, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    list-style: none;
    margin-right: 7px;
}

.languages__link {
    text-decoration: none;
    color: #1b1c21;;
}

.languages__link--active {
    color:#7e7e82;
}


@media screen and (1120px<=width) {
    .leftMenu {
        transition: all 1s;
    }

    .leftMenu--open {
        transition: all 0.7s;
    }
}

@media screen and (1440px<=width) {
    .leftMenu {
        left: 0;
        opacity: 1;
        box-shadow: 0 0 0 0 transparent;
    }

    .leftMenu__close {
        display: none;
    }
}